var EqcApp = angular.module("EqcApp", ["ui.router"]);

EqcApp.config(["$stateProvider", "$urlRouterProvider", function ($stateProvider, $urlRouterProvider) {
	$urlRouterProvider.otherwise("/");

    $stateProvider
		.state("main", {
			url: "/",
			controller: "MainController",
			templateUrl: "app/components/main/main.html"
		})
}]);

EqcApp.controller("MainController", ["$scope", function($scope) {

  $scope.message = "hei";
  
}]);
